@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial: 16px;
  --prime-color:#e81230;
  --startPage-background-color:#FFF6F3; }

* {
  margin: 0;
  padding: 0; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 16px)/1.35 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em; }

h5, h6 {
  font-weight: bold; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 2em; }

li {
  list-style-position: inside; }

a {
  text-decoration: none; }

input, textarea, select, button, option {
  font: var(--font-size-initial, 16px)/1.35 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

.btn {
  display: inline-block;
  border: 3px solid var(--prime-color, #e81230);
  border-radius: 5px;
  background: none;
  padding: 0.7em 2em;
  color: var(--prime-color, #e81230);
  text-decoration: none;
  font-size: 1.2em;
  text-align: center; }

.pennant {
  position: relative;
  display: inline-block;
  padding: 0.175em;
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  cursor: default;
  text-align: center;
  vertical-align: baseline; }

.icon {
  position: relative;
  display: inline-block;
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  color: var(--prime-color, #e81230);
  font-size: 1.2em;
  line-height: 2em;
  text-decoration: none;
  width: 2em;
  height: 2em;
  text-align: center; }

.icon:before {
  display: inline-block; }

.startPage-btn {
  padding: 0.5em 1em;
  color: var(--prime-color, #e81230);
  border: 1.5px solid var(--prime-color, #e81230);
  border-radius: 25px; }

.icon-nav {
  font-size: 1.2em;
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  color: var(--prime-color, #e81230);
  padding: 0.5em;
  text-align: center; }

.tabs {
  height: 45em;
  width: 100%;
  clear: both;
  position: relative;
  text-align: center;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  background: white;
  overflow: hidden; }

.tab p {
  padding: 1em;
  width: 100%;
  margin: 1em 0; }

.tab label {
  position: relative;
  background: white;
  color: var(--prime-color, #e81230);
  padding: 0.1em;
  font-size: 2em;
  display: flex; }

.tab-bodk-main label {
  font-size: 1.2em; }

.tab [type=radio] {
  display: none; }

.content {
  position: absolute;
  top: 8em;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  width: 100%; }

.content-bodk-main {
  position: absolute;
  top: 5em;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  text-align: left;
  width: 100%; }

[type=radio]:checked ~ label {
  z-index: 2; }

[type=radio]:checked ~ label ~ .content {
  z-index: 1; }

[type=radio]:checked ~ label ~ .content-bodk-main {
  z-index: 1; }

.wrapper {
  display: grid;
  grid-template-areas: "header" "main" "footer"; }

header {
  grid-area: header;
  justify-self: center;
  align-self: start; }

main {
  grid-area: main;
  justify-self: center;
  align-self: start;
  width: 80%;
  padding: 2em; }

footer {
  grid-area: footer;
  justify-self: center;
  align-self: start; }

.bodk-main-banner {
  width: 100%; }

#aboutUs {
  background: white; }

#home_text {
  background: var(--prime-color, #e81230);
  width: 100%;
  padding: 2em;
  margin: 5em auto; }

.startPage-img {
  width: 50%; }

.cover {
  background-image: url("/images/FON.svg");
  background-position: center center;
  background-origin: padding-box;
  background-repeat: no-repeat;
  background-size: auto;
  z-index: -100;
  border-bottom: 3px solid var(--prime-color, #e81230);
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  padding: 1em 4em;
  width: 80vw; }

header ul {
  list-style: none inside;
  padding: 0; }

header ul li {
  display: inline-block;
  margin: 0.1em; }

.home-text {
  margin: 0 0.1em;
  text-align: center; }

.home-text img {
  width: 100%;
  background: var(--prime-color, #e81230);
  padding: 1em; }

.social-net {
  display: flex;
  align-content: center;
  align-items: center;
  flex-direction: column; }

.phone {
  color: var(--prime-color, #e81230);
  font-size: 1.2em;
  padding: 0.2em; }

.cover-footer {
  background-image: url("/images/FON.svg");
  background-position: unset;
  background-repeat: no-repeat;
  z-index: -100;
  border-top: 3px solid var(--prime-color, #e81230);
  padding: 1em;
  text-align: center;
  width: 80vw;
  padding: 1em; }

.contactForm {
  text-align: center; }

#contact-form {
  width: 50%;
  padding: 2em;
  text-align: center;
  margin: 0 auto; }

#contact-form fieldset {
  border: none; }

#contact-form label {
  float: left;
  color: #e81230;
  display: none; }

#contact-form input[type=text],
#contact-form input[type=email],
#contact-form select,
#contact-form textarea {
  width: 100%;
  background: white;
  height: 3em;
  resize: none;
  border: none;
  border-bottom: 1.35px solid var(--prime-color, #e81230);
  margin: 1em 0; }

#contact-form textarea {
  resize: vertical;
  height: 100px; }

#contact-form input[type=submit] {
  background-color: #4CAF50;
  color: white;
  padding: 1em 2em;
  margin: 1em 0;
  border: none;
  border-radius: 5px;
  cursor: pointer; }

#contact-form input[type=text]:hover,
#contact-form input[type=email]:hover,
#contact-form select:hover,
#contact-form textarea:hover {
  transition: border-color 0.3s ease-in-out;
  border-bottom: 1px solid #AAA; }

#contact-form input[type=submit]:hover {
  transition: border-color 0.3s ease-in-out;
  border: 1px solid #AAA;
  background-color: #45a049; }

#contact-form input[type=submit]:active {
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5); }

#contact-form input:focus,
#contact-form textarea:focus {
  outline: 0;
  border: 1px solid #999; }
